import Image from "next/image";
import { Button } from "@heroui/button";
import { Product } from "@/types";
import { formatWhatsAppLink } from "@/lib/strapi";

interface ProductCardProps {
  product: Product;
}

function ProductCard({ product }: ProductCardProps) {
  const { name, description, price, image } = product.attributes;
  const imageUrl = image.data.attributes.url;
  const whatsappLink = formatWhatsAppLink(product);

  return (
    <div className="p-4 rounded-xl bg-white/80 backdrop-blur-sm border border-white/20 shadow-md">
      <div className="aspect-square relative rounded-lg overflow-hidden mb-4">
        <Image
          src={imageUrl}
          alt={name}
          fill
          className="object-cover"
        />
      </div>
      <h3 className="font-semibold text-lg">{name}</h3>
      <p className="text-gray-600 mb-2">{description}</p>
      <p className="text-lg font-semibold text-primary mb-4">â‚¹{price}</p>
      <Button 
        as="a"
        href={whatsappLink}
        target="_blank"
        rel="noopener noreferrer"
        className="w-full bg-primary text-white"
      >
        Enquire on WhatsApp
      </Button>
    </div>
  );
}

export async function ProductsSection() {
  const products = await getProducts();

  return (
    <section id="products" className="p-8 rounded-3xl bg-white/70 backdrop-blur-sm border border-white/20 shadow-lg">
      <h2 className="text-3xl font-bold mb-8 text-center">Featured Products</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {products.map((product) => (
          <ProductCard key={product.id} product={product} />
        ))}
      </div>
    </section>
  );
} 